// src/App.js
// (Alchan) -    

import React, { useEffect, Suspense, lazy, Component } from "react";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import { QueryClient, QueryClientProvider } from "@tanstack/react-query";
import { AuthProvider } from "./contexts/AuthContext";
//  [] ItemProvider AlchanLayout  (  )
import { ThemeProvider } from "./contexts/ThemeContext";
import { SkeletonPage } from "./components/ui/Skeleton";

//  React Query   - Firestore   
const queryClient = new QueryClient({
  defaultOptions: {
    queries: {
      //    : 30 (Firestore  )
      staleTime: 30 * 60 * 1000,
      //    : 2
      gcTime: 2 * 60 * 60 * 1000,
      //      refetch  ( )
      refetchOnWindowFocus: false,
      //     refetch 
      refetchOnReconnect: false,
      //     refetch  ( )
      refetchOnMount: false,
      //     1
      retry: 1,
      //     
      retryOnMount: false,
    },
  },
});

//   -   
const AlchanLayout = lazy(() => import("./components/AlchanLayout"));
const Login = lazy(() => import("./pages/auth/Login"));

//   (Tailwind   Tailwind  )
import "./styles.css";
import "./App.css";
import "./index.css"; // Tailwind CSS -  import  

//    - PWA  
class ErrorBoundary extends Component {
  constructor(props) {
    super(props);
    this.state = { hasError: false, error: null };
  }

  static getDerivedStateFromError(error) {
    return { hasError: true, error };
  }

  componentDidCatch(error, errorInfo) {
    console.error('[]   :', error, errorInfo);

    // IndexedDB       
    const errorString = error?.toString() || '';
    if (errorString.includes('IndexedDB') ||
        errorString.includes('QuotaExceeded') ||
        errorString.includes('SecurityError') ||
        errorString.includes('InvalidStateError')) {
      console.log('[]    -  ');
      this.clearCachesAndReload();
    }
  }

  clearCachesAndReload = async () => {
    try {
      // IndexedDB 
      if (window.indexedDB) {
        const databases = await window.indexedDB.databases?.() || [];
        databases.forEach(db => {
          if (db.name) {
            window.indexedDB.deleteDatabase(db.name);
          }
        });
      }

      //  
      if ('caches' in window) {
        const names = await caches.keys();
        await Promise.all(names.map(name => caches.delete(name)));
      }

      //  
      localStorage.clear();
      sessionStorage.clear();

      // 
      window.location.reload(true);
    } catch (e) {
      console.error('[]   :', e);
      window.location.reload(true);
    }
  };

  render() {
    if (this.state.hasError) {
      return (
        <div style={{
          minHeight: '100vh',
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          justifyContent: 'center',
          background: 'linear-gradient(135deg, #7c3aed 0%, #4f46e5 100%)',
          color: 'white',
          padding: '20px',
          textAlign: 'center'
        }}>
          <div style={{ fontSize: '48px', marginBottom: '16px' }}></div>
          <h1 style={{ fontSize: '24px', marginBottom: '8px' }}>    </h1>
          <p style={{ marginBottom: '24px', opacity: 0.8 }}>    ...</p>
          <button
            onClick={this.clearCachesAndReload}
            style={{
              padding: '12px 24px',
              background: 'white',
              color: '#4f46e5',
              border: 'none',
              borderRadius: '12px',
              fontSize: '16px',
              fontWeight: 'bold',
              cursor: 'pointer'
            }}
          >
              
          </button>
        </div>
      );
    }

    return this.props.children;
  }
}

function App() {
  // FCM    
  useEffect(() => {
    if ('serviceWorker' in navigator) {
      navigator.serviceWorker.ready.then(registration => {
        if (registration.active) {
          const firebaseConfig = {
            apiKey: process.env.REACT_APP_FIREBASE_API_KEY,
            authDomain: process.env.REACT_APP_FIREBASE_AUTH_DOMAIN,
            projectId: process.env.REACT_APP_FIREBASE_PROJECT_ID,
            storageBucket: process.env.REACT_APP_FIREBASE_STORAGE_BUCKET,
            messagingSenderId: process.env.REACT_APP_FIREBASE_MESSAGING_SENDER_ID,
            appId: process.env.REACT_APP_FIREBASE_APP_ID
          };
          registration.active.postMessage({
            type: 'FIREBASE_CONFIG',
            config: firebaseConfig
          });
        }
      }).catch(err => {
        console.warn('[]    ():', err);
      });
    }
  }, []);

  return (
    <ErrorBoundary>
      <QueryClientProvider client={queryClient}>
        <ThemeProvider>
          <AuthProvider>
            {/*  [] ItemProvider  - AlchanLayout      */}
            <Router>
              <Suspense fallback={<SkeletonPage />}>
                <Routes>
                  <Route path="/login" element={<Login />} />
                  <Route path="/*" element={<AlchanLayout />} />
                </Routes>
              </Suspense>
            </Router>
          </AuthProvider>
        </ThemeProvider>
      </QueryClientProvider>
    </ErrorBoundary>
  );
}

export default App;
